body {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

main {
  justify-content: center;
  display: flex;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header__logo-link {
  width: 40px;
  height: 40px;
  display: inline-block;
}

.nav {
  justify-content: center;
  align-items: center;
  height: 60px;
  display: flex;
}

.nav .nav__list {
  text-transform: uppercase;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav .nav__link {
  color: #060b35;
  justify-content: center;
  align-items: center;
  height: 60px;
  margin-right: 20px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  transition: color .3s;
  display: flex;
  position: relative;
}

.nav .nav__link:hover {
  color: #00acdc;
}

.nav .nav__item {
  display: inline-block;
}

.nav .nav__item:last-child .nav__link {
  margin-right: 0;
}

.nav .is-active {
  color: #00acdc;
  position: relative;
}

.nav .is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.container {
  --column-count: 1;
  grid-template-columns: repeat(var(--column-count), 200px);
  grid-gap: 46px 48px;
  justify-content: center;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .container {
    --column-count: 2;
  }
}

@media (width >= 768px) {
  .container {
    --column-count: 3;
  }
}

@media (width >= 1024px) {
  .container {
    --column-count: 4;
  }
}

.card {
  box-sizing: border-box;
  text-align: center;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  width: 200px;
  padding: 32px 16px 16px;
  transition: transform .3s;
  display: flex;
}

.card:hover {
  transform: scale(1.2);
}

.card__title {
  text-align: left;
  color: #060b35;
  margin-top: 0;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition: color .3s;
}

.card:hover .card__title {
  color: #34568b;
}

.card__product {
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
  display: flex;
}

.card__image {
  max-width: 100%;
  height: 100%;
}

.card__code {
  text-align: left;
  color: #616070;
  margin-top: 0;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__review {
  color: main;
  justify-content: space-between;
  align-items: center;
  height: 16px;
  margin-bottom: 24px;
  font-weight: 400;
  display: flex;
}

.card__review-count {
  margin: 0;
  font-size: 10px;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 16px;
  display: flex;
}

.card__price-title {
  color: #616070;
  margin: 0;
  padding: 0;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-total {
  color: #060b35;
  margin: 0;
  padding: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__buy-link {
  color: #fff;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  margin: 0;
  padding: 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: background-color .3s, color .3s;
  display: flex;
}

.card__buy-link:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  gap: 4px;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars--4 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}
